"use client";

import React, { useState } from "react";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";

interface Plan {
  id: string;
  name: string;
  price: string;
  features: string[];
  isPro: boolean;
}

const plans: Plan[] = [
  {
    id: "free",
    name: "Free Plan",
    price: "$0 / month",
    features: [
      "Basic travel accounting features",
      "Manage clients, invoices, expenses",
      "Access to reports"
    ],
    isPro: false,
  },
  {
    id: "pro",
    name: "Pro Plan",
    price: "$29 / month",
    features: [
      "All Free Plan features",
      "Priority support",
      "Advanced analytics and reports",
      "AI-powered expense categorization",
      "Multi-user collaboration"
    ],
    isPro: true,
  },
];

export function SubscriptionPlans() {
  const [currentPlan, setCurrentPlan] = useState<string>("free");
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState<string | null>(null);

  const handleSubscribe = async (planId: string) => {
    setLoading(true);
    setMessage(null);
    try {
      // Simulate API call delay
      await new Promise((resolve) => setTimeout(resolve, 1000));
      setCurrentPlan(planId);
      setMessage(`Successfully subscribed to the ${planId === "pro" ? "Pro Plan" : "Free Plan"}.`);
    } catch (error) {
      setMessage("Subscription failed. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="space-y-6 max-w-4xl mx-auto">
      <h2 className="text-2xl font-semibold text-center">Choose Your Subscription Plan</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {plans.map((plan) => (
          <Card key={plan.id} className={`p-6 border ${currentPlan === plan.id ? "border-blue-600" : "border-gray-300"}`}>
            <h3 className="text-xl font-bold mb-2">{plan.name}</h3>
            <p className="text-lg text-gray-700 mb-4">{plan.price}</p>
            <ul className="mb-6 list-disc list-inside space-y-1 text-gray-600">
              {plan.features.map((feature, idx) => (
                <li key={idx}>{feature}</li>
              ))}
            </ul>
            <Button
              onClick={() => handleSubscribe(plan.id)}
              disabled={loading || currentPlan === plan.id}
              className="w-full"
            >
              {loading && currentPlan !== plan.id ? "Processing..." : currentPlan === plan.id ? "Current Plan" : "Subscribe"}
            </Button>
          </Card>
        ))}
      </div>
      {message && (
        <p className="text-center mt-4 text-sm text-green-600">{message}</p>
      )}
    </div>
  );
}
